<template>
  <div id="app">
    <div class="header">
      <!--업로드-->
      <div v-if="step==2" v-on:click="addFeed">
        <ul v-on:click="nextStep" class="header-button-right">
          <li>발행</li>
        </ul>
      </div>
      <!--필터-->
      <div v-if="step==1">
        <ul class="header-button-left">
          <li>Cancel</li>
        </ul>
        <ul v-on:click="nextStep" class="header-button-right">
          <li>Next</li>
        </ul>
      </div>
      <img src="./assets/logo.png" class="logo" />
    </div>
    <Body v-on:전송이벤트="쓴글=$event" v-bind:포스팅들="포스팅들" v-bind:step="step" v-bind:업로드이미지="업로드이미지" />
    <div class="footer">
      <ul class="footer-button-plus">
        <input type="file" id="file" class="inputfile" v-on:change="upload" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>
</template>

<script>
import Body from "./components/Body.vue";
import postData from "./assets/postdata.js";

export default {
  name: "App",
  data() {
    return {
      포스팅들: postData,
      //step:0-post.vue , 1 - filter , 2-write
      step: 0, //status
      업로드이미지: "",
      쓴글: "",
      date: new Date()
    };
  },
  props: {
    포스티들: Array
  },
  components: {
    Body: Body
  },
  methods: {
    upload(e) {
      //옛날 방식 : 서버에 보내고 url 받기
      //요즘 방식 ; 브라우저 내에서 사진 저장 filereader readAsDataUrl
      var file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
        this.step = 1;
        this.업로드이미지 = e.target.result;
      };
    },
    nextStep() {
      this.step = 2;
    },
    addFeed() {
      this.step = 0;
      var OBJ = {
        name: "Soo",
        userImage: "https://placeimg.com/100/100/sea",
        postImage: this.업로드이미지,
        date: this.date,
        liked: false,
        caption: this.쓴글,
        filter: "perpetua"
      };
      this.포스팅들.unshift(OBJ);

      //img, text-area data {}에저장
      //{}를 this.Data.js에 추가
    }
  }
};
</script>

